var that;
export default {
	data() {
		return {
			c: true,
			checked: false,
			list: [],
			form: '',
			onlist: false,
			modal: false
		};
	},
	methods: {
		hideModal(i) {
			that.modal = !that.modal;
			if (i == 1) {
				that.delid = '';
			}
		},
		checkedclear(e) {
			this.checked = e.detail.value;
		},
		del(e) {
			that.delid = e.currentTarget.dataset.id;
			that.hideModal();
		},
		// 删除地址
		deleteAddress(e) {
			let id = e.currentTarget.dataset.id;
			uni.showModal({
				title: '提示',
				content: '确定要删除该地址吗？',
				success(res) {
					if (res.confirm) {
						that.$request
							.request({
								url: that.api.user.address_delete,
								data: {
									address_id: id
								},
								Toast: '正在删除'
							})
							.then(res => {
								that.$alertok('删除成功');
								that.getaddress();
							});
					}
				}
			});
		},
		getaddress() {
			that.onlist = false;
			this.$request.request({
				url: that.api.user.address_list,
				Toast: '正在加载...',
				success(res) {  
					uni.hideLoading()
					that.list = res.list;
					if (res.list.length == 0) {
						that.list = true;
					}
				}
			});
		},
		// 设置默认地址
		setDefaultAddress(e) {
			this.$request
				.request({
					url: that.api.user.address_set_default,
					data: {
						address_id: e.currentTarget.dataset.id
					}
				})
				.then(res => {
					that.$alert('设置成功');
					setTimeout(that.getaddress(), 1000);
				});
		},
		// 选择地址
		print(e) {
			if (that.form) {
				uni.setStorageSync('address', that.list[e.currentTarget.dataset.index]);
				uni.navigateBack({
					delta: 1
				});
			}
		},

		// 编辑
		setaddress(e) {
			var address = JSON.stringify(that.list[e.currentTarget.dataset.index]);
			// console.log(address);
			uni.navigateTo({
				url: 'addressinfo/addressinfo?address=' + address
			});
		}
	},
	onLoad(option) {
		that = this;
		that.form = option.form ? option.form : '';
	},
	onShow() {
		that.getaddress();
	}
};
